html {
  background-color: #f5f5f5;
}

body {
  overflow-x: hidden;
  display: grid;
  grid-template-columns: 64px 264px 1fr;
  grid-template-rows: auto;
}

.skip-link {
  position: absolute;
  top: 2.5rem;
  left: 1rem;
  transform: translateX(-100vw);
  transition: transform 0.3s ease;
  z-index: 4;
}
@media (min-width: 60rem) {
  .skip-link {
    top: 1rem;
  }
}
.skip-link:focus {
  transform: translateX(0);
}

img {
  height: auto;
  max-width: 100%;
}

nav ul {
  list-style: none;
  padding-left: 0;
  margin-bottom: 0;
}
nav ul li {
  padding-left: 0;
  padding-bottom: 0;
  margin-bottom: 0;
  line-height: 1;
}
nav ul li::before {
  display: none;
}
nav ul li a {
  border: 0;
}
nav ul li a:not(.btn) {
  border: 0;
}

html.noscroll {
  overflow: hidden;
}
html.noscroll.overlay {
  position: relative;
}

