button, .btn {
  --button--border-color: var(--color-primary);
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  color: #FFFFFF;
  line-height: 1.5rem;
  padding: 0.625rem 1.25rem;
  background-color: #005f6b;
  border-radius: 0.5rem;
  transition: background-color 0.3s ease;
  cursor: pointer;
  text-align: center;
}
button.btn-outline, .btn.btn-outline {
  background-color: transparent;
  color: #005f6b;
  border-color: #e57c32;
}
button.btn-outline.green, .btn.btn-outline.green {
  border-color: #7a4f94;
}
button.btn-wide, .btn.btn-wide {
  width: 100%;
}
button:hover, button:focus, .btn:hover, .btn:focus {
  background-color: rgb(0, 140.2803738318, 158);
}
button > svg, .btn > svg {
  fill: #FFFFFF;
  margin-right: 0.2rem;
}

aside.sidebar.main {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 0.75rem;
  padding: 1.5rem 0.75rem;
  box-sizing: border-box;
  height: 100vh;
  border-right: 1px solid #e0e0e0;
}
aside.sidebar.main nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  gap: 0.75rem;
}
aside.sidebar.main nav .link {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 0.3rem;
}
aside.sidebar.main nav .link span {
  font-size: 0.75rem;
}

aside.sidebar.dynamic {
  height: 100vh;
  display: flex;
  gap: 3rem;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  padding: 1.5rem 1.75rem;
}
aside.sidebar.dynamic nav {
  display: flex;
  flex-direction: column;
  gap: 0.1rem;
  justify-content: start;
  width: 100%;
}
aside.sidebar.dynamic nav .link {
  width: 100%;
  padding: 0.3rem 0.5rem;
  border-radius: 0.5rem;
  overflow: hidden;
  display: inline-block;
  white-space: nowrap;
  text-overflow: ellipsis;
}
aside.sidebar.dynamic nav .link:hover, aside.sidebar.dynamic nav .link.active {
  background-color: #f2a76d;
}

.content {
